.food-place-icons {
    align-items: center;
    margin-bottom: 1rem;
    > a {
        border: solid 1px var(--color-primary-5);
        border-radius: 100%;
        width: 2rem;
        height: 2rem;
        margin-right: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    a:last-child {
        margin-right: 0;
    }
}
